@media only screen and (min-width: 320px) {

    body.blog {
        background-image: url(http://localhost:8888/erica/wp-content/uploads/2018/02/Scan_20160123-8.png);
        background-size: cover;
        background-position: -100px 0%;
        background-repeat: no-repeat;
        z-index: 0;
        
        .content-area {
            background-color: rgba(0, 0, 0, 0.3);
            height: 100%;

        }

    }

    .post {

        text-align: left-aligned;
        margin: 50px 50px 0 50px;

        .blog-title {
            font-family: $font_heading;
            color: white; 
            font-size: 3em;
            margin-bottom: 10px;
        }
        
        .entry-header {

            .blog-category:first-of-type {
                margin-top: 20px;
            }

            a {
                list-style-type: none;
                color: white;
                background-color: $beige;
                padding: 10px 25px;
                text-transform: uppercase;
                letter-spacing: .10em;
                font-size: 10px;
            }
        }

        .entry-meta {
            color: $grey;
            font-family: $font_body;
            margin-bottom: 20px;
        }

        img {
            margin-bottom: 20px;
            margin-right: 2%;
        }

        p {
            color: white;
        }
    }
}


@media only screen and (min-width: 780px) {

    .post {


        .entry-content {
            margin-top: 0;

            .news-image-containter {
                margin-top: 0;
            }

        .entry-meta {
            margin-bottom: 0;
        }
        
        }
    }

}